.App{
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 0%;
  margin: 0%;
  background-color: #1E90FF;
}

.header{
  flex: 30%;
  background-color: #9400D3;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header .inputContainer{
  display: flex;
  flex-direction: row;
  gap: 10;
}

.header input{
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 8px;
  padding-left: 10px;
  font-size: 17px;
  border: 1px solid white;
  margin: 5px;
}

.header button{
  width: 100px;
  height: 40px;
  margin-top: 30px;
  border: 1px solid blue;
  background-color: green;
  border-radius: 10px;
  cursor: pointer;
}

.todoList{
  flex: 70%;
  width: 100%;
  display: flex;
  align-items: center;
  paddding-top: center;
  flex-direction: column;
}

.task{
  width: 500px;
  height: 50px;
  display: flex;
  color: white;
  margin: 15px;
}

.task .content {
  flex: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.task .content span {
  display: grid;
  place-items: center;
  border: 1px solid white;
  width: 100%;
  height: 100%;
  font-size: 18px;
  border-right: none;
  background-color: tomato;
}

.task button {
  flex: 20%;
  height: 100%;
  border: none;
  background-color: blue;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  color: white;
  cursor: pointer;
}